
.page-header ~ div.page-container {
  bottom: 0;
}
.page-header.detail-header {
  color: #fff;
  border-bottom: none;
  transition: all 1s;
}
.page-header.detail-header .mint-header-title {
  color: #fff;
  font-size: 16px;
}
.movieThumb {
  height: 290px;
  box-sizing: border-box;
  padding: 20px;
  text-align: center;
  transition: all 1s;
}
.movieThumb__img {
  height: 100%;
}
.movieInfo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  box-sizing: border-box;
  padding: 25px 15px;
}
.movieInfo__text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  box-sizing: border-box;
  padding-right: 50px;
}
.movieInfo__title {
  font-size: 27px;
  font-weight: bold;
}
.movieInfo__directors {
  font-size: 12px;
  color: #999;
}
.movieInfo__directors i {
  font-style: normal;
}
.movieInfo__casts {
  font-size: 12px;
  color: #999;
}
.movieInfo__casts i {
  font-style: normal;
}
.movieInfo__rating {
  width: 85px;
  height: 85px;
  box-shadow: 0 0 10px #e9e8e5;
  text-align: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.movieInfo__num {
  display: block;
  font-size: 17px;
  color: #333;
  font-weight: bold;
  text-align: center;
  padding-top: 16px;
}
.movieInfo__star {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  text-align: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.movieInfo__img {
  width: 10px;
  height: 10px;
}
.movieInfo__count {
  display: block;
}
.movieIntro {
  padding: 15px;
  position: relative;
}
.movieIntro:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  background: #e4e4e4;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.movieIntro__title {
  padding: 15px 0;
  font-size: 17px;
  font-weight: normal;
  color: #333;
}
.movieIntro__content {
  font-size: 15px;
}
.movieIntro__open {
  color: #42bd56;
}
.mask {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10;
}



