
.filmInfo {
  background: #fff;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  transition: all 0.8s cubic-bezier(0.55, 0, 0.1, 1);
}
.filmInfo__header {
  padding: 15px;
}
.filmInfo__close {
  display: block;
  width: 20px;
  height: 20px;
}
.filmInfo__closeimg {
  width: 100%;
  height: 100%;
}
.filmInfo__content {
  padding: 15px;
}
.filmInfo__item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.filmInfo__label {
  color: #9b9b9b;
  font-size: 14px;
  width: 50px;
}
.filmInfo__con {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #555;
  font-size: 14px;
}
.filmInfo__con i {
  font-style: normal;
}



