
.m-tabbar-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.m-tabbar-item .router-link-active .m-tabbar-item-icon {
  display: none;
}
.m-tabbar-item .router-link-active .m-tabbar-item-icon.active {
  display: block;
}
.m-tabbar-item .router-link-active .m-tabbar-item-text {
  color: #42bd56;
}
.m-tabbar-item .m-tabbar-item-icon {
  display: block;
  padding-top: 2px;
}
.m-tabbar-item .m-tabbar-item-icon.active {
  display: none;
}
.m-tabbar-item .m-tabbar-item-icon img {
  width: 28px;
  height: 28px;
}
.m-tabbar-item .m-tabbar-item-text {
  display: block;
  font-size: 10px;
  color: #949494;
}



